.navLink {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  position: relative;
  padding: 2vh;
  text-decoration: none;

}
.navLink span {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.navLink::before {
  content: ' ';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  border-top-width: 1px;
  border-bottom-width: 1px;
  border-top-style: solid;
  border-bottom-style: solid;
  border-top-color: rgba(255,255,255,0.5);
  border-bottom-color: rgba(255,255,255,0.5);
  -webkit-transform: scale(0.1, 1);
          transform: scale(0.1, 1);
}
.navLink:hover span {
  letter-spacing: 2px;
}
.navLink:hover::before {
  opacity: 1;	
  -webkit-transform: scale(1, 1);	
          transform: scale(1, 1);	
  
}
.navLink::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
  background-color: transparent;

}
.navLink:hover::after {
  opacity: 0;	
  -webkit-transform: scale(0.1, 1);	
          transform: scale(0.1, 1);
  
}
.navLink:visited{
  color: rgb(179,221,242);
}



